import React, {Component, Fragment} from 'react';
import Icon from '../assets/Icon';

class AddMoney extends Component{
  render(){
    return(
      <Fragment>
        <h4>¿Cuánto dinero deseas añadir a tu cartera?</h4>
        <label><span style={{fontWeight: "bold"}} >$/.</span> <input type="number" id="add-money" /></label>
        <button id="button-add-money"><Icon iconName="fas fa-plus" iconStyle={{padding: "0", color:"black"}}/> &nbsp;Añadir dinero</button>
      </Fragment>
    )
  }
}

export default AddMoney;